import Vue from "vue";
import VueRouter, { RouteConfig } from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes: Array<RouteConfig> = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/login",
    name: "Login",
    component: () => import("../views/Login.vue"),
  },
  {
    path: "/register",
    name: "Register",
    component: () => import("../views/Register.vue"),
  },
  {
    path: "/profile",
    name: "Profile",
    component: () => import("../views/Profile.vue"),
  },
  {
    path: "/admin",
    name: "Admin",
    component: () => import("../views/admin/Admin.vue"),
  },
  {
    path: "/admin/user/edit/:userIdx",
    name: "AdminUserEdit",
    component: () => import("../views/admin/AdminUserEdit.vue"),
  },
  {
    path: "/about",
    name: "About",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/About.vue"),
  },
  {
    path: "*",
    name: "PostView",
    component: () => import("../views/forum/PostView.vue"),
  },
  {
    path: "/forum/:category",
    name: "PostList",
    component: () => import("../views/forum/PostList.vue"),
  },
  {
    path: "/user/posts/:userIdx",
    name: "UserPostList",
    component: () => import("../views/forum/PostList.vue"),
  },
  {
    path: "/user/comments/:userIdx",
    name: "UserCommentList",
    component: () => import("../views/forum/CommentList.vue"),
  },
  {
    path: "/edit/:idOrCategory", // @todo id => idx
    name: "PostEdit",
    component: () => import("../views/forum/PostEdit.vue"),
  },
  {
    path: "/cafe/create",
    name: "CafeCreate",
    component: () => import("../views/cafe/CafeCreate.vue"),
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
