
















import Vue from "vue";
import Component from "vue-class-component";
import router from "@/router";
import TopMenuUser from "./components/TopMenuUser.vue";
import MobileHeader from "./components/MobileHeader.vue";
import DesktopRightSidebar from "./components/DesktopRightSidebar.vue";
import DesktopMainMenuRight from "./components/desktop/DesktopMainMenuRight.vue";
import { AppService } from "./service/app.service";
import { ApiService } from "./x-vue/services/api.service";
import { Err } from "./x-vue/services/defines";
import DesktopHeader from "./components/desktop/DesktopHeader.vue";

@Component({
  components: {
    TopMenuUser,
    MobileHeader,
    DesktopRightSidebar,
    DesktopMainMenuRight,
    DesktopHeader,
  },
})
export default class App extends Vue {
  app = AppService.instance;
  api = ApiService.instance;

  async mounted(): Promise<void> {
    // console.log("App::mounted. running mode: ", process.env.NODE_ENV);
    this.app.portal();
    try {
      await this.api.loadCafe();
    } catch (e: unknown) {
      if (e === Err.main_cafe_has_no_cafe_category_record) {
        // If the cafe category record does not exists,
        // then, it might be the root site of the cafe is not being created.
        // this.app.error(e);
        console.log(e);
      } else if (e === Err.cafe_not_exists) {
        this.app.error("@todo set dev environment to move to local main cafe");
      } else {
        this.app.error(e);
      }
    }
    router.afterEach((to) => {
      if (
        to.name == "PostList" ||
        to.name == "PostView" ||
        to.name == "Admin"
      ) {
        this.app.hideLeftSidebar();
      } else {
        this.app.showLeftSidebar();
      }
    });
  }
}
