import Vue from "vue";
import App from "./App.vue";
import "./registerServiceWorker";
import router from "./router";
import store from "./store";

import {
  AvatarPlugin,
  AlertPlugin,
  ButtonPlugin,
  ProgressPlugin,
  SpinnerPlugin,
  FormTextareaPlugin,
  // IconsPlugin,
  NavbarPlugin,
  FormInputPlugin,
  PopoverPlugin,
  TabsPlugin,
  CardPlugin,
  ModalPlugin,
  PaginationNavPlugin,
} from "bootstrap-vue";

// Import Bootstrap an BootstrapVue CSS files (order is important)
import "bootstrap/dist/css/bootstrap.css";
import "bootstrap-vue/dist/bootstrap-vue.css";

import { AppService } from "./service/app.service";
import Cookies from "js-cookie";
import $ from "jquery/dist/jquery.slim";

import "./scss/index.scss";

import { tr } from "@/x-vue/services/functions";

Vue.config.productionTip = false;

// Make BootstrapVue available throughout your project
Vue.use(AvatarPlugin);
Vue.use(AlertPlugin);
Vue.use(ButtonPlugin);
Vue.use(ProgressPlugin);
// Vue.use(IconsPlugin);
Vue.use(NavbarPlugin);
Vue.use(FormInputPlugin);
Vue.use(SpinnerPlugin);
Vue.use(FormTextareaPlugin);
Vue.use(PopoverPlugin);
Vue.use(TabsPlugin);
Vue.use(CardPlugin);
Vue.use(ModalPlugin);
Vue.use(PaginationNavPlugin);

// Optionally install the BootstrapVue icon components plugin
// Vue.use(IconsPlugin);

const app = AppService.instance;

Vue.filter("t", tr);

const vm = new Vue({
  router,
  store,
  data: {
    a: "apple",
    landing: true,
  },
  render: (h) => h(App),
  mounted: () => {
    ``;
  },
  methods: {
    aTag: function (href: string) {
      this.$router.push(href).catch(() => null);
    },
    changeLanguage: function (v: string) {
      app.setCookie("language", v);
      location.reload();
    },
  },
}).$mount("#app");

store.state.vm = vm;

$("a").click(function (e) {
  e.preventDefault();
  vm.aTag($(this).attr("href") as string);
});

$(function () {
  const ln = Cookies.get("language");
  if (ln) {
    $(".desktop-topmenu [name='language']").val(ln);
  }
});
